  $color-cyan-water :#C9E6EF;
  $font-weight3: 300;
  $font-weight4: 400;
  $font-weight5: 500;
  $font-weight6: 600;
  $font-weight7: 700;
  $font-weight8: 800;
  * {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }
  p {
    padding-top: 0;
    margin: 0 0 10px;
  }
  display: block;
  .root {
    border-radius: 2px;
    // .helper {
    //   width: 0;
    //   height: 0;
    //   overflow: hidden;
    //   visibility: hidden;
    //   div {
    //     width: 9999px;
    //   }
    //   span {
    //     padding: 8px;
    //   }
    // }

    .header {
      // font-size: $font-size2;
      // color: $color-black;
      font-weight: $font-weight6;
      display: flex;
      align-items: center;
      p {
        word-break: break-word;
      }
      calcite-icon{
        margin: 0 4px;
        top: 4px;
        position: relative;
      }
    }

    .job-info {
      // border:  $border-style;
      position: relative;
      padding: 10px;
      border-radius: 2px;

      &.hasDetail {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
      }

      .status-icon {
        position: absolute;
        top: 14px;
        left: 10px;

        // :host-context(html[dir='rtl']) & {
        //   left: unset;
        //   right: 10px;
        // }
      }

      .detail {
        width: 100%;
        padding-left: 24px;

        // :host-context(html[dir='rtl']) & {
        //   padding-right: 24px;
        //   padding-left: unset;
        // }

        .header {
          padding-right: 40px;

          display: block;
          word-break: break-word;
          // :host-context(html[dir='rtl']) & {
          //   padding-right: unset;
          //   padding-left: 40px;
          // }

          ::ng-deep {
            i {
              display: inline;
              position: relative;
              top: 3px;
            }
          }
        }

        .icon-area {
          position: absolute;
          top: 10px;
          right: 10px;

          // :host-context(html[dir='rtl']) & {
          //   left: 10px;
          //   right: auto;
          // }
          a {
            width: 16px;
            text-align: center;
            display: inline-block;
            margin: 0 5px;
            color: var(--calcite-color-text-2);
            &:last-child {
              margin-right: 0;
              // :host-context(html[dir='rtl']) & {
              //   margin-left: 0;
              //   margin-right: 5px;
              // }
            }
            &.isloading {
              cursor: default;
              i {
                cursor: default;
              }
            }
          }
          a.disabled {
            cursor: not-allowed;

            i {
              cursor: not-allowed;

              ::ng-deep {
                svg path {
                  fill-opacity: 0.6;
                }
              }
            }
          }

          // display: none;
          i {
            cursor: pointer;

            &.download-icon {
              // margin-right: 10px;

              // :host-context(html[dir='rtl']) & {
              //   margin-left: 10px;
              //   margin-right: unset;
              // }
              &::ng-deep {
                .spinner .icon-spin{
                  margin-left: 0;
                  margin-right: 0;
                }
            }
            &.spinner {
              position: relative;
              top: -2px;
            }
            }
            ::ng-deep {
              i {
                line-height: 0;
              }
            }
          }
          .menu-item {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 224px;
            margin-left: -12px;
            // min-width: 200px;
            // max-width: 400px;
            >a {
              // padding-right: 60px;
              width: calc(100% - 60px); //100%;
              text-align: left;
              display: inline-block;
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
            }

            >span {
              // position: absolute;
              right: 10px;
            }
          }

          // .multilple-file-container {
          //   position: absolute;
          //   max-width: 400px;
          //   max-height: 200px;
          //   min-width: 200px;
          //   left: unset;
          //   right: 0;
          //   border-radius: 2px;
          //   overflow: auto;

          //   div {
          //     line-height: 40px;
          //     height: 40px;

          //     >a {
          //       padding-right: 60px;
          //       width: 100%;
          //       display: inline-block;
          //       white-space: nowrap;
          //       overflow: hidden;
          //       text-overflow: ellipsis;
          //     }

          //     >span {
          //       position: absolute;
          //       right: 10px;
          //     }
          //   }

          //   li>a {
          //     padding: 0px 10px;
          //     // font-weight: $font-weight4;
          //   }
          // }
        }

        .middle-line {
          overflow: hidden;

          .records {
            width: 42%;
            float: inline-start;
          }

          .startTime {
            width: 55%;
            float: inline-end;
            text-align: right;

            // :host-context(html[dir='rtl']) & {
            //   text-align: left;
            // }
          }
        }

        .status {
          display: flex;
          justify-content: space-between;
          margin-bottom: 0px;
          >span {
            width: 58%;
            display: inline-block;
          }

          a.job-detail-link {
            width: 35%;
            margin-left: 5%;
            cursor: pointer;
            // color: $color-green-forest;
            text-decoration: underline;
            text-align: right;
            display: inline-flex;
            justify-content: flex-end;
            align-items: center;

            // :host-context(html[dir='rtl']) & {
            //   margin-right: 5%;
            //   margin-left: unset;
            // }

            &.job-detail-pending {
              // color: #000000;
              cursor: default;
              text-decoration: none;

              i.icon-spin {
                // display: table-cell;
                // line-height: 0;
                // position: relative;
                // top: 7px;
                margin-left: 2px;

                // :host-context(html[dir='rtl']) & {
                //   margin-left: unset;
                //   margin-right: 2px;
                // }

                .spinner{
                  position: relative;
                  top: -7px;            
                }
              }
            }
          }
        }

        .confirm-delete-anchor,.cancel-delete-anchor {
          // color: $color-black;
          cursor: pointer;
          text-decoration: underline;
        }
        .cancel-delete-anchor {
          margin-left: 20px;
          // :host-context(html[dir='rtl']) & {
          //   margin-left: auto;
          //   margin-right: 20px;
          // }
        }
      }
    }

    .job-detail {
      // border: $border-style;
      border-top: none;
      border-top-left-radius: 0;
      border-top-right-radius: 0;
      position: relative;
      border-radius: 2px;
    }

    &:hover {
      .job-info .detail .icon-area {
        display: block;
      }
    }

    // modal
    ::ng-deep {
      .modal-body {
        padding: 0;


      }
    }

  }
}

:host-context([dir="rtl"]) {
  .job-info {
    .status-icon {
      left: unset!important;
      right: 10px;
    }
  }
  .detail{
    padding-right: 24px;
    padding-left: unset!important;
    .header {
      padding-right: unset!important;
      padding-left: 40px;
    }
    .icon-area {
      left: 10px;
      right: auto !important;
      a:last-child {
        margin-left: 0;
        margin-right: 5px;
      }
    }

    .middle-line {
      .startTime {
        text-align: left;
      }
    }
    .status {
      a.job-detail-link {
        margin-right: 5%;
        margin-left: unset!important;
        &.job-detail-pending {
          i.icon-spin {
            margin-left: unset!important;
            margin-right: 2px;
          }
        }
      }
    }
    .cancel-delete-anchor {
      margin-left: auto!important;
      margin-right: 20px;
    }
  }
}