import { Component, Host, h, getAssetPath, Prop, State } from '@stencil/core';
import { UtilService } from '../../../services/util.service';

@Component({
  tag: 'report-icon',
  styleUrl: 'report-icon.scss',
  shadow: true,
})
export class ReportIcon {

  @Prop() icon: any;
  @Prop() size: any;

  @State() pathData: any;

  componentWillLoad() {
    this.fetchIcon().then((res) => {
      this.pathData = res;
    })
  }

  fetchIcon() {
    const utils: UtilService = UtilService.getService();
    return Promise.resolve(true)
    .then(() => {
      if (utils.getSvgCache(this.icon)) {
        return utils.getSvgCache(this.icon);
      } 
      return fetch(getAssetPath(`./assets/icons/${this.icon}.svg`))
        .then((resp) => {
          if (!resp.ok) {
            throw new Error("could not get the icon file:");
          }
          return resp.text();
        })
      }).then((str) => {
        utils.setSvgCache(this.icon, str);
        const div = document.createElement('div');
        div.innerHTML = str;
        if (this.size) {
          div.querySelector('svg').setAttribute('width', this.size);
          div.querySelector('svg').setAttribute('height', this.size);
        }
        return div.innerHTML;
      })
  }

  render() {
    return (
      <Host>
        <i innerHTML={this.pathData} >
        </i>
        <slot></slot>
      </Host>
    );
  }

}
