import { Component, Fragment, Host, Element, Prop, State,  h, Event, EventEmitter } from '@stencil/core';
import { TranslateService } from '../../../services/translate.service';
import { UtilService } from '../../../services/util.service';
import { ReportService } from '../../../services/report.service';
// import { PropsService } from '../../../services/props.service';
import { StateService } from '../../../services/state.service';

@Component({
  tag: 'task-info',
  styleUrl: 'task-info.scss',
  shadow: true,
})
export class ReportTaskInfo {
  @Prop() job: any;
  @Prop() showDetail: string;
  @Prop() detailedStatus: string;
  @Prop() detailedStatusEle: string;

  @State() isWaitingToConfirmRemove: boolean;
  @State() isRemoving: boolean;
  @State() isCanceling: boolean;
  @Event() jobRemoved: EventEmitter<any>;
  
  @Element() host: HTMLElement;
  
  public isRTL: boolean = false;
  public jobId: string;
  private langTasks: any;
  private langCommon: any;
  // private helperSpan: HTMLElement;

  public statusI18nConfig: any;
  public maxFailedOIDLength = 120;
  private utils: UtilService = UtilService.getService();
  private reportService: ReportService = ReportService.getService();
  private stateService: StateService = StateService.getService();

  // todo: should show basic info first, then get the detail
  componentWillLoad() {
    this.jobId = this.job?.jobId;
    return Promise.resolve(true)
    // .then(() => {
    //   return TranslateService.getService().getTranslate();
    // })
    .then(() => {
      const res = TranslateService.getService().getTranslateSync();
      const langTasks = res?.customPrint.recentTasks;
      this.langCommon = res?.common;
      this.langTasks = langTasks;
      this.statusI18nConfig = {
        esriJobWaiting: langTasks.jobStatusWaiting,
        esriJobSubmitted: langTasks.jobStatusSubmitted,
        esriJobExecuting: langTasks.jobStatusExecuting,
        esriJobSucceeded: langTasks.jobStatusSucceeded,
        esriJobFailed: langTasks.jobStatusFailed,
        pdfConverting: langTasks.pdfConverting,
        pdfConverted: langTasks.pdfConverted,
        dfConvertFailed: langTasks.dfConvertFailed
      };
      this.stateService.subscribe('job-updated', (data) => {
        if (data?.jobId === this.job?.jobId) {
          this.job = {...data};
          this.jobId = this.job?.jobId;
          this.updateJobStatusString();
        }
      })
      return true;
    }).then(() => {
      // do not add "return" here, otherwise the component will not render until status request finished.
      this.reportService.checkJobDetails(this.job).then(() => {
        this.jobId = this.job?.jobId;
        this.detailedStatus = this.job.jobStatusInfo?.detailedStatus;
        this.updateJobStatusString();
        return this.job;
      })
    });

  }

  public getPercentage(p) {
    const utilService: UtilService = UtilService.getService();
    return utilService.getPercentage(this.job, p);
  }

  public updateJobStatusString() {
    this.detailedStatus = this.job.jobStatusInfo?.detailedStatus;
    const i18nMapping = {
      preparing: 'jobStatusPrinting',
      'printing,printed,printFailed': 'jobStatusPrinting',
      'pdfConverting,pdfConverted,pdfConvertFailed': 'jobStatusConverting',
      'merging': 'jobStatusMerging',
      'packaging,packaged,packageFailed': 'jobStatusPackaging',
      'uploading,uploadFailed': 'jobStatusUploading'
    };
    const matchedKey = Object.keys(i18nMapping).find((key) => {
      return key.split(',').indexOf(this.detailedStatus) >= 0;
    })
    const i18nStr = this.langTasks[i18nMapping[matchedKey]] || '';
    if (!this.job.jobStatus) {
      this.detailedStatusEle ='';
    }
    if (['esriJobWaiting','esriJobSubmitted','esriJobExecuting'].indexOf(this.job.jobStatus) > -1) {
      if (this.job.jobStatus=='esriJobExecuting' && this.job.jobStatusInfo && this.detailedStatus) {
        const progress = this.detailedStatus === 'preparing' ? 0 : null
        const progressPercent = this.getPercentage(progress);
        this.detailedStatusEle = <span>{i18nStr.replace('${$completedPercent}', `${progressPercent}`)}</span>;
      }
    } else {
      if (this.job.jobStatus=='esriJobExecuting' && (!this.job.jobStatusInfo || !this.detailedStatus)) {
        this.detailedStatusEle = <span>{this.langTasks.jobStatusPrinting}</span>;
      } else {
        this.detailedStatusEle = <span>{this.statusI18nConfig[this.job.jobStatus]}</span>;
      }
    }
  }

  public getDuration(start, end, cancelled?) {
    const hourUnit = this.langCommon.timeUnitHour;
    const miniUnit = this.langCommon.timeUnitMinute;
    const secUnit = this.langCommon.timeUnitSecond;
    let duration = '';
    if (!end && cancelled) {
      end = cancelled;
    }
    const miliSecond = end - start;
    if (miliSecond > 0) {
      const seconds = Math.round(miliSecond / 1000);
      const hours = Math.floor(seconds / 3600);
      const minus = Math.floor((seconds % 3600) / 60);
      const sec = seconds % 60;
      if (hours > 0) {
        duration += hours + hourUnit + ' ';
      }
      if (minus > 0 || hours > 0) {
        duration += minus + miniUnit + ' ';
      }
      duration += sec + secUnit;
    }
    return duration;
  }

  public showDetailHandler() {
    if (!this.job.resultInfo?.resultFiles && !this.job.resultInfo?.details) {
      this.showDetail = 'pending';
      this.reportService.checkJobStatus(this.job.jobId).then((statusObj) => {
        // this.state.trigger('report-updateJobDetail', statusObj);
        // this.updateFailedOIDlength(statusObj);
        this.job = statusObj;
        this.jobId = this.job.jobId;
        this.showDetail = 'done';
        // this.showDetail();
      });
    } else {
      // this.updateFailedOIDlength(this.job);
      this.showDetail = 'done';
      // this.showDetail();
    }
  }

  public hideDetail() {
    this.showDetail = '';
  }

  /**
   * execute removing job
   * @param job 
   */
  public executeRemoveJob(job) {
    // if (job._tempRuntime) {
    //   delete this.isWaitingToConfirmRemove;
    // }
    // job._tempRuntime = job._tempRuntime || {};
    this.isWaitingToConfirmRemove = false;
    this.isRemoving = true;
    // https://devtopia.esri.com/Beijing-R-D-Center/feature-report/issues/239#issuecomment-3550777
    this.reportService.removeJob(job.jobId).then((res) => {
      if (!res || !res.success) {
        console.log('Remove job failed', (res.error && res.error.details && res.error.details.length) ? res.error.details.join('.') : '');
        this.reportService.showError({}, {errorStr: res.error?.message , detail:res.error?.detail, showDetails: !!res.error?.detail});
        // this.alertService.setOption({
        //   alertType: 'danger',
        //   html: (res.error && res.error.message) ? res.error.message : 'Failed to remove the task.',
        //   detail: (res.error && res.error.details && res.error.details.length) ? res.error.details.join('<br>') : ''
        // }).show();
      } else {
        // this.state.trigger('report-remove-job', job);
        // todo: tell the parent component to remove this job
        // this.reportService.removeJobFromList(job);
        this.jobRemoved.emit(job);
        this.isWaitingToConfirmRemove = false;
      }
      this.isRemoving = false;
    }).catch(() => {
      // toto: use string to replace the hard coded 'Failed to remove the task.' in 3.16
      // this.customReportService.showError(res, {errorStr: 'Failed to remove the task.', showDetails: true});
      // this.alertService.setOption({
      //   alertType: 'danger',
      //   html: 'Failed to remove the task.',
      //   detail: (res && res.error && res.error.message) ? res.error.message : ''
      // }).show();      
      this.isRemoving = false;

    });  
  }

  /**
   * cancel removing job
   * @param job 
   */
  public cancelRemoveJob() {
    this.isWaitingToConfirmRemove = false;
  }

  /**
   * remove job
   * @param job 
   */
  public startRemoveJob() {
    this.isWaitingToConfirmRemove = true;
  }

  private startCancelJob(job) {
    this.isCanceling = true;
    // https://devtopia.esri.com/Beijing-R-D-Center/feature-report/issues/239#issuecomment-3550777
    this.reportService.cancelJob(job.jobId).then((res) => {
      if (!res || !res.success) {
        console.log('Cancel job failed');
        this.reportService.showError({}, {errorStr: res.error?.message , detail:res.error?.detail, showDetails: !!res.error?.detail});
        // this.customReportService.showError(res, {errorStr: this.recentTasksString.cancelJobFailMsg, detail: ((res.error && res.error.details && res.error.details.length) ? res.error.details.join('<br>') : '')});
      } else {
        // success to cancel job
        // this.state.trigger('report-cancel-job', job);
        job.jobStatus = 'esriJobStatusCancelled';
        job = {...job};
      }
      delete job._tempRuntime.isCanceling;
    }).catch(() => {
      // this.customReportService.showError(res, {errorStr: this.recentTasksString.cancelJobFailMsg, showDetails: true});
      // this.alertService.setOption({
      //   alertType: 'danger',
      //   html: 'Failed to cancel the task.',
      //   detail: (res && res.error && res.error.message) ? res.error.message : ''
      // }).show();

      this.isCanceling = false;
    });
  }

  render() {
    return (
      <Host>
        <calcite-card class={`root ${!this.job ? 'hidden' : ''} `}>

          { this.job ? 
              <div class={`job-info ${this.showDetail ? 'hasDetail' : ''}`}>

              <i class="status-icon">
                {/* executing start */}
                {/* todo: show tooltip */}
                {['esriJobWaiting','esriJobSubmitted','esriJobExecuting'].indexOf(this.job.jobStatus)>=0 ? <calcite-icon scale="s" icon="hourglass-active" text-label={this.statusI18nConfig[this.job.jobStatus]}></calcite-icon> : ''}
                { this.job.jobStatus=='esriJobSucceeded' ? <report-icon icon="success"></report-icon> : ''}
                {/* { this.job.jobStatus=='esriJobSucceeded' ? <calcite-icon scale="s" icon="check-circle" text-label={this.langTasks.jobStatusSucceeded}></calcite-icon> : ''} */}
                {/* todo: implement the partial succeed icon */}
                { this.job.jobStatus=='esriJobPartialSucceeded' ? <report-icon icon="success-caveat"></report-icon> : ''}
                {/* { this.job.jobStatus=='esriJobPartialSucceeded' ? <calcite-icon scale="s" icon="check-circle-f" text-label={this.langTasks.jobStatusPartialSucceeded1}></calcite-icon> : ''} */}
                { this.job.jobStatus=='esriJobStatusCancelled' ? <calcite-icon scale="s" icon="circle-disallowed" text-label={this.langTasks.jobStatusCanceled}></calcite-icon> : ''}
                { this.job.jobStatus=='esriJobFailed' ? <calcite-icon scale="s" icon="x-circle" text-label={this.langTasks.jobStatusFailed}></calcite-icon> : ''}
              </i>

              <div class="detail">
                <p class="header">
                  { this.job.resultInfo ? this.job.resultInfo.title : ''}
                  {/* todo: in the rtl layout, the icon should be flipped */}
                  { this.job.inputInfo&&this.job.inputInfo.parameters&&this.job.inputInfo.parameters.outputFormat=='pdf' ? 
                  <calcite-icon scale="s" icon={this.isRTL ? '' : 'file-pdf'} text-label=""></calcite-icon> 
                  : <calcite-icon scale="s" icon={this.isRTL ? '' : 'file-word'} text-label=""></calcite-icon>}
                </p>

                { !(this.isWaitingToConfirmRemove) ? 
                  <p class="middle-line">
                    <span class="records">{this.langTasks.records}{this.job.resultInfo&&this.job.resultInfo.totalCount?this.job.resultInfo.totalCount:''}</span>
                    <span class="startTime" title={this.utils.formatDateTime(this.job.submitted, 'datetime')}>{this.langTasks.started}{this.utils.formatDateTime(this.job.submitted, 'time')}</span>
                  </p> : ''
                }

                { !(this.isWaitingToConfirmRemove) ? 
                  <p class="status">
                    { ['esriJobWaiting','esriJobSubmitted','esriJobExecuting'].indexOf(this.job.jobStatus) > -1 ? 
                      <span>{this.langTasks.status} {this.detailedStatusEle} </span>
                      : 
                      <Fragment>
                        { ['esriJobWaiting','esriJobSubmitted','esriJobExecuting'].indexOf(this.job.jobStatus) == -1 ?
                          <span>
                            {this.langTasks.duration} {this.getDuration(this.job.submitted, this.job.completed, this.job.cancelled)}
                          </span> : ''
                        }
                      </Fragment>
                    }
                    
                  { this.job.jobStatus=='esriJobPartialSucceeded'||this.job.jobStatus=='esriJobFailed' ? 
                    <Fragment>
                      {!this.showDetail ? 
                        <a class="job-detail-link"
                          onClick={() => this.showDetailHandler()}>{this.langCommon.showDetails}</a>
                        : ''
                      }
                      {this.showDetail=='pending' ? 
                        <a class="job-detail-link job-detail-pending">
                          {this.langCommon.loading}
                          <i class="icon icon-spin disabled">
                            <calcite-loader label="" inline="true"></calcite-loader>
                            {/* <survey123-ui-spinner class="spinner" [iconColor]="'#666'"></survey123-ui-spinner> */}
                          </i>
                        </a> : ''
                      }

                      {this.showDetail=='done' ?
                        <a class="job-detail-link"
                        onClick={() => this.hideDetail()}>{this.langCommon.hideDetails}</a>
                        : ''
                      }

                    </Fragment>
                  : ''}
                  </p> : 
                  <Fragment>
                    {this.isWaitingToConfirmRemove ? 
                      <Fragment>
                        <p>{this.langTasks.removeJobConfirm}</p>
                        <p>
                          <a class="confirm-delete-anchor" onClick={() => this.executeRemoveJob(this.job)}>{this.langCommon.yes}</a>
                          <a class="cancel-delete-anchor" onClick={() => this.cancelRemoveJob()}>{this.langCommon.no}</a>
                        </p>
                      </Fragment> : ''
                    }
                  </Fragment>
                }
                {!this.isWaitingToConfirmRemove ? 
                  <div class="icon-area dropdown">
                   {/* url expired, cannot download */}
                   {this.job.jobStatus=='esriJobSucceeded'&&this.job.urlExpired ? 
                    <a>
                      <calcite-icon scale="s" icon="download" text-label="" disabled id={this.jobId + '-disabled-btn'}></calcite-icon>
                      <calcite-tooltip label={this.langTasks.downloadDisabledTip} reference-element={this.jobId + '-disabled-btn'}>
                        <span>{this.langTasks.downloadDisabledTip}</span>
                      </calcite-tooltip>
                    </a>: ''
                  }

                  {/* download */}
                  {(this.job.jobStatus=='esriJobSucceeded'||this.job.jobStatus=='esriJobPartialSucceeded')&&this.job.url&&!this.job.urlExpired&&!this.job.multipleFiles ?
                    <a href={this.job.url}  title={this.langCommon.download} download target={this.job.inputInfo&&this.job.inputInfo.parameters&&this.job.inputInfo.parameters.outputFormat=='pdf'?'_blank':''}>
                      <i class="download-icon">
                        <calcite-icon scale="s" icon="download" text-label=""></calcite-icon>
                      </i>
                    </a>
                    : ''
                  }

          
                  {/* download multiple files */}
                  {(this.job.jobStatus=='esriJobSucceeded'||this.job.jobStatus=='esriJobPartialSucceeded')&&this.job.multipleFiles&&!this.job.urlExpired ? 
                    <a class="dropdown-toggle">
                    <calcite-dropdown width="m" placement="bottom-end" type="click">
                      <i  slot="trigger" class="download-icon" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">
                        <calcite-icon scale="s" icon="download" text-label=""></calcite-icon>
                      </i>
                      {this.job.resultInfo.resultFiles.map((file) => {
                          return <calcite-dropdown-item>
                                  <div class="menu-item">
                                    <a href={file.url} title={file.name}>
                                      <span class="left-text">{file.name || ''}</span>
                                    </a>
                                    <span class="right-text">{file.size || ''}</span>
                                  </div>
                                </calcite-dropdown-item>;
                        })}
                    </calcite-dropdown>
                    </a> : ''  
                  }

          
                  {/* cancel job, job can be cancled before uploading process */}
                  { ['esriJobSubmitted', 'esriJobExecuting', 'esriJobWaiting'].indexOf(this.job.jobStatus)>=0 &&!(this.job.jobStatus=='esriJobExecuting'&&this.job.jobStatusInfo&&this.detailedStatus&&['uploading', 'uploadFailed', 'uploadSucceeded', 'usageSent', 'usageSentFailed', 'skipCharging'].indexOf(this.detailedStatus)>=0) ?
                    <a class={this.isCanceling ? 'isloading' : ''} title={(this.isCanceling)?'':`${this.langTasks.cancelJobTip}`}>
                      { !(this.isCanceling) ? 
                        <i class="download-icon" onClick={() => this.startCancelJob(this.job)}>
                          <calcite-icon scale="s" icon="circle-stop" text-label=""></calcite-icon>
                        </i>
                        :
                        <i class="download-icon spinner">
                          {/* todo: icon color is #666666 */}
                          <calcite-loader label={this.langTasks.jobStatusCanceled} inline="true"></calcite-loader>
                        </i>
                      }
                    </a>
                    : ''
                  }

                  {/* remove job */}
                  { ['esriJobSucceeded', 'esriJobPartialSucceeded', 'esriJobFailed', 'esriJobStatusCancelled'].indexOf(this.job.jobStatus)>=0 ? 
                    <a class={this.isRemoving ? 'isloading' : ''} title={(this.isRemoving)?'':`${this.langTasks.removeJobTip}`}>
                      {this.isRemoving}
                      {!this.isRemoving ? 
                        <i class="download-icon" onClick={() => this.startRemoveJob()}>
                          <calcite-icon scale="s" icon="trash" ></calcite-icon>
                        </i>
                        :
                        <i class="download-icon spinner">
                        {/* todo: icon color is #666666 */}
                          <calcite-loader label="" inline="true"></calcite-loader>
                        </i>
                      }
                    </a>
                    : null
                  }

                </div> : ''
                }
                

              </div>
            </div> : ''
          }
          {this.job&&this.showDetail=='done' ? 
            <div class="job-detail">
              <detail-table job={this.job}></detail-table>
            </div>
            : ''
          }

          </calcite-card>
        <slot></slot>
      </Host>
    );
  }

}
