import { Component, Host, Element, State, Prop, h } from '@stencil/core';
import { TranslateService } from '../../../services/translate.service';
import { UtilService } from '../../../services/util.service';

@Component({
  tag: 'detail-table',
  styleUrl: 'detail-table.scss',
  shadow: true,
})
export class ReportDetailTable {
  @Prop() job: any;
  @State() errorMsg: string;
  @Element() host: HTMLElement;
  private langTasks: any;

  public statusI18nConfig: any;

  componentWillLoad() {
    const utilService: UtilService = UtilService.getService();
    this.errorMsg = utilService.parseMarkdown(this.job.messages&&this.job.messages.length?this.job.messages.join('<br>'):'');
    return Promise.resolve(true)
    // .then(() => {
    //   return TranslateService.getService().getTranslate();
    // })
    .then(() => {
      const res = TranslateService.getService().getTranslateSync();
      const langTasks = res?.customPrint?.recentTasks;
      this.langTasks = langTasks;
      this.statusI18nConfig = {
        esriJobWaiting: langTasks.jobStatusWaiting,
        esriJobSubmitted: langTasks.jobStatusSubmitted,
        esriJobExecuting: langTasks.jobStatusExecuting,
        esriJobSucceeded: langTasks.jobStatusSucceeded,
        esriJobFailed: langTasks.jobStatusFailed,
        pdfConverting: langTasks.pdfConverting,
        pdfConverted: langTasks.pdfConverted,
        dfConvertFailed: langTasks.dfConvertFailed
      };
    })

  }

  public getPercentage(p) {
    const utilService: UtilService = UtilService.getService();
    return utilService.getPercentage(this.job, p);
  }

  /**
   * open the error detailed modal
   */
  private errorDetailModalOpen() {
    const modal = this.host.shadowRoot?.querySelector('.error-modal');
    (modal as any).open = true;
  }

  /**
   * open the success detailed modal
   */
  private succeedDetailModalOpen() {
    const modal = this.host.shadowRoot?.querySelector('.success-modal');
    (modal as any).open = true;
  }


  render() {
    return (
      <Host>
        <calcite-table caption="job details" striped>
            <calcite-table-row>
                <calcite-table-cell>{this.langTasks.detailJobID}</calcite-table-cell>
                <calcite-table-cell>{this.job.jobId}</calcite-table-cell>
            </calcite-table-row>
            <calcite-table-row>
                <calcite-table-cell>{this.langTasks.detailStatus}</calcite-table-cell>
                <calcite-table-cell>
                <div>
                  <span>{this.statusI18nConfig[this.job.jobStatus]}</span>
                  {this.job.jobStatus=='esriJobPartialSucceeded'&&this.job.resultInfo.totalCount ? 
                    <span>{this.langTasks.jobStatusPartialSucceeded.replace('${$completedPercent}', `${this.getPercentage(1-this.job.resultInfo.failedObjectIds.length/this.job.resultInfo.totalCount)}`)}
                    </span>: ''
                  }
                </div>
              </calcite-table-cell>
            </calcite-table-row>
            <calcite-table-row>
                <calcite-table-cell>{this.langTasks.detailStatusMsg}</calcite-table-cell>
                <calcite-table-cell>
                  <div innerHTML={this.errorMsg} title={this.errorMsg}>
                  </div>
                </calcite-table-cell>
            </calcite-table-row>
            <calcite-table-row>
                <calcite-table-cell>{this.langTasks.detailFailedOIDs} {( this.job.resultInfo&&this.job.resultInfo.failedObjectIds&&this.job.resultInfo.failedObjectIds.length ? ' (' + this.job.resultInfo.failedObjectIds.length + ') ' : '' ) }</calcite-table-cell>
                <calcite-table-cell>
                  {this.job.resultInfo&&this.job.resultInfo.failedObjectIds&&this.job.resultInfo.failedObjectIds.length ?
                    <div>
                      <p class="max-tow-lines" title={this.job.resultInfo.failedObjectIds.join(',')}>
                        {this.job.resultInfo.failedObjectIds.join(',')}
                      </p>
                        
                      {this.job.resultInfo.details&&this.job.resultInfo.details.length ? 
                        <p class="more-details">
                          <a onClick={() => this.errorDetailModalOpen()}>{this.langTasks.detailMoreDetails}</a>
                        </p> : ''
                      }
                  </div>
                  : ''
                }
              </calcite-table-cell>
            </calcite-table-row>
            <calcite-table-row>
                <calcite-table-cell>{this.langTasks.detailSucceededOIDs} {this.job.resultInfo&&this.job.resultInfo.succeededObjectIds&&this.job.resultInfo.succeededObjectIds.length?this.job.resultInfo.succeededObjectIds.length:''}</calcite-table-cell>
                <calcite-table-cell>
                  {this.job.resultInfo&&this.job.resultInfo.succeededObjectIds&&this.job.resultInfo.succeededObjectIds.length ? 
                    <div>
                      <p class="max-tow-lines" title={this.job.resultInfo.succeededObjectIds.join(',')}>
                        {this.job.resultInfo.succeededObjectIds.join(',')}</p>
                      <p class="more-details">
                        <a onClick={() => this.succeedDetailModalOpen()}>{this.langTasks.detailMoreDetails}</a>
                      </p>
                    </div>: ''
                  }
                </calcite-table-cell>
            </calcite-table-row>
        </calcite-table>

        {/* <table class="table table-striped">
          <tr>
            <td>{this.langTasks.detailJobID}</td>
            <td>
              <div>{this.job.jobId}</div>
            </td>
          </tr>
          <tr>
            <td>{this.langTasks.detailStatus}</td>
            <td>
              <div>
                <span>{this.statusI18nConfig[this.job.jobStatus]}</span>
                {this.job.jobStatus=='esriJobPartialSucceeded'&&this.job.resultInfo.totalCount ? 
                  <span>{this.langTasks.jobStatusPartialSucceeded.replace('${$completedPercent}', `${this.getPercentage(1-this.job.resultInfo.failedObjectIds.length/this.job.resultInfo.totalCount)}`)}
                  </span>: ''
                }
              </div>
            </td>
          </tr>
          <tr>
            <td>{this.langTasks.detailStatusMsg}</td>
            <td>
              <div title={this.errorMsg} innerHTML={this.errorMsg}></div>
            </td>
          </tr>
          <tr>
            <td>
              {this.langTasks.detailFailedOIDs} {( this.job.resultInfo&&this.job.resultInfo.failedObjectIds&&this.job.resultInfo.failedObjectIds.length ? ' (' + this.job.resultInfo.failedObjectIds.length + ') ' : '' ) }
            </td>
            <td>
              {this.job.resultInfo&&this.job.resultInfo.failedObjectIds&&this.job.resultInfo.failedObjectIds.length ?
                  <div>
                    <p class="max-tow-lines" title={this.job.resultInfo.failedObjectIds.join(',')}>
                      {this.job.resultInfo.failedObjectIds.join(',')}
                    </p>
                      
                    {this.job.resultInfo.details&&this.job.resultInfo.details.length ? 
                      <p class="more-details">
                        <a onClick={() => this.errorDetailModalOpen()}>{this.langTasks.detailMoreDetails}</a>
                      </p> : ''
                    }
                </div>
                : ''
              }
            </td>
          </tr>
          <tr>
            <td>
              {this.langTasks.detailSucceededOIDs} {this.job.resultInfo&&this.job.resultInfo.succeededObjectIds&&this.job.resultInfo.succeededObjectIds.length?this.job.resultInfo.succeededObjectIds.length:''}
            </td>
            <td>
            {this.job.resultInfo&&this.job.resultInfo.succeededObjectIds&&this.job.resultInfo.succeededObjectIds.length ? 
              <div>
                <p class="max-tow-lines" title={this.job.resultInfo.succeededObjectIds.join(',')}>
                  {this.job.resultInfo.succeededObjectIds.join(',')}</p>
                <p class="more-details">
                  <a onClick={() => this.succeedDetailModalOpen()}>{this.langTasks.detailMoreDetails}</a>
                </p>
              </div>: ''
            }

            </td>
          </tr>
        </table> */}


        <error-detail-modal class="error-modal" job={this.job}></error-detail-modal>

        <success-detail-modal class="success-modal" job={this.job}></success-detail-modal>
        <slot></slot>
      </Host>
    );
  }

}
