:host {
  display: block;
  calcite-table {
    width: 100%;
  }
  calcite-table-cell {
    .max-tow-lines {
      word-wrap: break-word;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      max-height: 40px;
      margin-bottom: 5px;
    }
    .more-details{
      margin-bottom: 0px;
      text-decoration: underline;
      cursor: pointer;
    }

  }
  table {
    width: 100%;
    margin-bottom: 0px;
    tr {
      border-top: 1px solid #EFF0F5;
      &:nth-child(2n+1) {
        background-color: #F9FBFE;
      }
      td {
        border: none;
        padding: 6px 5px;
        >div {
          // max-width: 300px;
          word-wrap: break-word;
        }

        .max-tow-lines {
          word-wrap: break-word;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          max-height: 40px;
          margin-bottom: 5px;
        }
        .more-details{
          margin-bottom: 0px;
          cursor: pointer;
        }

        &:first-child {
          padding-left: 30px;
          max-width: 120px;
          vertical-align: top;

          // :host-context(html[dir='rtl']) & {
          //   padding-right: 30px;
          //   padding-left: unset;
          // }
        }

        a {
          cursor: pointer;
          // color: $color-green-forest;
          text-decoration: underline;
          // font-weight: $font-weight5;
        }
      }
    }
  }
}

:host-context([dir="rtl"]) {
  table tr td:first-child{
    padding-right: 30px;
    padding-left: unset !important;
  }
}
